import {
    GET_MOVIES,
    GET_MOVIES_INIT,
     MOVIES_ERROR,
  } from '../actions/types';

  const initialState = {
    movies: [],
    loading: false,
    error: {},
    title:""
    
  };

function reducer(state=initialState,action) {

    const {type , payload} = action;
    
   switch(type){
            case GET_MOVIES:
                   
                return{
                    ...state,
                    movies: payload.movies,
                    title:payload.title,
                    loading:false
                    
                };
           case GET_MOVIES_INIT:         
           return{
               ...state,              
               loading:true
               
           };

           case MOVIES_ERROR:
            
            return{
                ...state,
                error:payload
            };

        default: 
            return state;
   }
}

export default reducer
